<template>
	<view class="cart-container" v-if="cart.length !== 0">
    <!-- 收货地址组件 -->
    <my-address></my-address>
		<!-- 购物车商品列表的标题区域 -->
    <view class="cart-title">
      <!-- 左侧的图标 -->
      <uni-icons type="shop" size="18"></uni-icons>
      <!-- 描述文本 -->
      <text class="cart-title-text">购物车</text>
    </view>
    <!-- /购物车商品列表的标题区域 -->
    <!-- 商品的的列表区域 -->
    <uni-swipe-action>
      <block v-for="(item, i) in cart" :key="i">
      <uni-swipe-action-item :options="options" @click="swipeItemClickHandler(item)">
        <my-goods :goods="item" :showRadio="true" :showNum="true" @radio-change="radioChangeHandler" @num-change="numberChange"></my-goods>
      </uni-swipe-action-item>
      </block>
    </uni-swipe-action>
    <!-- /商品的的列表区域 -->
    
    <!-- 结算区域 -->
    <my-settle></my-settle>
    <!-- /结算区域 -->
	</view>
  
  <!-- 空购物车区域 -->
  <view class="empty-cart" v-else>
    <image src="/static/cart_empty@2x.png" class="empty-img"></image>
    <text class="tip-text">空空如也~</text>
  </view>
  <!-- /空购物车区域 -->
</template>

<script>
  import badgeMix from '@/mixins/tabbar-badge.js'
  // 按需导入mapState
  import {mapState,mapMutations} from 'vuex'
	export default {
    mixins:[badgeMix],
    computed:{
      // 将m_cart模块中的cart数组映射到当前页面中使用
      ...mapState('m_cart',['cart'])
    },
		data() {
			return {
				options:[
				        {
				            text: '删除', //显示文本的内容
				            style: {
				                backgroundColor: '#C00000' //按钮的背景颜色
				            }
				        }
				      ]
			};
		},
    methods:{
      ...mapMutations('m_cart',['updateGoodsState','updateGoodsCount','removeGoodsById']),
      radioChangeHandler(e){
        // console.log(e,233)
        // 商品的勾选状态发生了变化
        this.updateGoodsState(e)
      },
      // 商品数量发生变化
      numberChange(e){
        // 拿到商品的数量变化
        // console.log(e,'瓜') 
        // 持久化到本地
        this.updateGoodsCount(e)
      },
      swipeItemClickHandler(goods){
        // console.log(goods)
        this.removeGoodsById(goods.goods_id)
      }
    }
	}

</script>

<style lang="scss">
 .cart-container {
   margin-bottom: 50px;
 }
 .cart-title {
  height: 40px;
  display: flex;
  align-items: center;
  font-size: 14px;
  padding-left: 5px;
  border-bottom: 1px solid #efefef;
  .cart-title-text {
    margin-left: 10px;
  }
}
.empty-cart {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 150px;
  .empty-img {
    width: 90px;
    height: 90px;
  }
  .tip-text {
    font-size: 12px;
    color: gray;
    margin-top: 15px;
  }
}
</style>
